import * as firebase from 'firebase'

export default {
  state: {
    loadedEvents: [
      {
        imageUrl: 'https://d2v9y0dukr6mq2.cloudfront.net/video/thumbnail/VCHXZQKsxil3lhgr4/animation-loading-circle-icon-on-white-background-with-alpha-channel-4k-video_sjujffkcde_thumbnail-full01.png',
        id: 'greruh3',
        title: 'Loading',
        date: new Date(),
        location: 'Loading',
        description: 'Loading'
      }
    ],
    loadedPastEvents: [
      {
        imageUrl: 'https://d2v9y0dukr6mq2.cloudfront.net/video/thumbnail/VCHXZQKsxil3lhgr4/animation-loading-circle-icon-on-white-background-with-alpha-channel-4k-video_sjujffkcde_thumbnail-full01.png',
        id: 'greruh3',
        title: 'Loading',
        date: new Date(),
        location: 'Loading',
        description: 'Loading'
      }
    ],
    loadedAllEvents: [
      {
        imageUrl: 'https://d2v9y0dukr6mq2.cloudfront.net/video/thumbnail/VCHXZQKsxil3lhgr4/animation-loading-circle-icon-on-white-background-with-alpha-channel-4k-video_sjujffkcde_thumbnail-full01.png',
        id: 'greruh3',
        title: 'Loading',
        date: new Date(),
        location: 'Loading',
        description: 'Loading'
      }
    ]
  },
  mutations: {
    setLoadedEvents (state, payload) {
      state.loadedEvents = payload
    },
    setLoadedPastEvents (state, payload) {
      state.loadedPastEvents = payload
    },
    setLoadedAllEvents (state, payload) {
      state.loadedAllEvents = payload
    },
    createEvent (state, payload) {
      state.loadedAllEvents.push(payload)
      location.reload()
    },
    updateEvent (state, payload) {
      const allevent = state.loadedAllEvents.find(allevent => {
        return allevent.id === payload.id
      })
      if (payload.title) {
        allevent.title = payload.title
      }
      if (payload.description) {
        allevent.description = payload.description
      }
      if (payload.date) {
        allevent.date = payload.date
      }
      const event = state.loadedEvents.find(event => {
        return event.id === payload.id
      })
      if (payload.title) {
        event.title = payload.title
      }
      if (payload.description) {
        event.description = payload.description
      }
      if (payload.date) {
        event.date = payload.date
      }
      const pastevent = state.loadedPastEvents.find(pastevent => {
        return pastevent.id === payload.id
      })
      if (payload.title) {
        pastevent.title = payload.title
      }
      if (payload.description) {
        pastevent.description = payload.description
      }
      if (payload.date) {
        pastevent.date = payload.date
      }
      this.$store.dispatch('loadPastEvents')
      this.$store.dispatch('loadEvents')
      this.$store.dispatch('loadAllEvents')
    }
  },
  actions: {
    loadEvents ({commit}) {
      commit('setLoading', true)
      firebase.database().ref('events').once('value')
        .then((data) => {
          const events = []
          const obj = data.val()
          for (let key in obj) {
            var currentdate = new Date()
            var datetime = currentdate.getFullYear() + '-' + (currentdate.getMonth() + 1) + '-' + currentdate.getDate() + ' ' + currentdate.getHours() + ':' + currentdate.getMinutes() + ':' + currentdate.getSeconds()
            var currentdatecompare = Date.parse(datetime)
            var eventdate = obj[key].date
            var eventdatecompare = Date.parse(eventdate)
            if (currentdatecompare < eventdatecompare) {
              events.push({
                id: key,
                title: obj[key].title,
                description: obj[key].description,
                imageUrl: obj[key].imageUrl,
                date: obj[key].date,
                location: obj[key].location,
                creatorId: obj[key].creatorId
              })
            }
          }
          commit('setLoadedEvents', events)
          commit('setLoading', false)
        })
        .catch(
          (error) => {
            console.log(error)
            commit('setLoading', false)
          }
        )
    },
    loadPastEvents ({commit}) {
      commit('setLoading', true)
      firebase.database().ref('events').once('value')
        .then((data) => {
          const events = []
          const obj = data.val()
          for (let key in obj) {
            var currentdate = new Date()
            var datetime = currentdate.getFullYear() + '-' + (currentdate.getMonth() + 1) + '-' + currentdate.getDate() + ' ' + currentdate.getHours() + ':' + currentdate.getMinutes() + ':' + currentdate.getSeconds()
            var currentdatecompare = Date.parse(datetime)
            var eventdate = obj[key].date
            var eventdatecompare = Date.parse(eventdate)
            if (currentdatecompare > eventdatecompare) {
              events.push({
                id: key,
                title: obj[key].title,
                description: obj[key].description,
                imageUrl: obj[key].imageUrl,
                date: obj[key].date,
                location: obj[key].location,
                creatorId: obj[key].creatorId
              })
            }
          }
          commit('setLoadedPastEvents', events)
          commit('setLoading', false)
        })
        .catch(
          (error) => {
            console.log(error)
            commit('setLoading', false)
          }
        )
    },
    loadAllEvents ({commit}) {
      commit('setLoading', true)
      firebase.database().ref('events').once('value')
        .then((data) => {
          const events = []
          const obj = data.val()
          for (let key in obj) {
            events.push({
              id: key,
              title: obj[key].title,
              description: obj[key].description,
              imageUrl: obj[key].imageUrl,
              date: obj[key].date,
              location: obj[key].location,
              creatorId: obj[key].creatorId
            })
          }
          commit('setLoadedAllEvents', events)
          commit('setLoading', false)
        })
        .catch(
          (error) => {
            console.log(error)
            commit('setLoading', false)
          }
        )
    },
    createEvent ({commit, getters}, payload) {
      const event = {
        title: payload.title,
        location: payload.location,
        description: payload.description,
        date: payload.date.toISOString(),
        creatorId: getters.user.id
      }
      let imageUrl
      let key
      firebase.database().ref('events').push(event)
        .then((data) => {
          key = data.key
          return key
        })
        .then(key => {
          const filename = payload.image.name
          const ext = filename.slice(filename.lastIndexOf('.'))
          return firebase.storage().ref('events/' + key + '.' + ext).put(payload.image)
        })
        .then(fileData => {
          imageUrl = fileData.metadata.downloadURLs[0]
          return firebase.database().ref('events').child(key).update({imageUrl: imageUrl})
        })
        .then(() => {
          commit('createEvent', {
            ...event,
            imageUrl: imageUrl,
            id: key
          })
        })
        .catch((error) => {
          console.log(error)
        })
    },
    updateEventData ({commit}, payload) {
      commit('setLoading', true)
      const updateObj = {}
      if (payload.title) {
        updateObj.title = payload.title
      }
      if (payload.description) {
        updateObj.description = payload.description
      }
      if (payload.date) {
        updateObj.date = payload.date
      }
      firebase.database().ref('events').child(payload.id).update(updateObj)
        .then(() => {
          commit('setLoading', false)
          commit('updateEvent', payload)
        })
        .catch(error => {
          console.log(error)
          commit('setLoading', false)
        })
    }
  },
  getters: {
    loadedEvents (state) {
      return state.loadedEvents.sort((eventA, eventB) => {
        return eventA.date > eventB.date
      })
    },
    loadedPastEvents (state) {
      return state.loadedPastEvents.sort((eventA, eventB) => {
        return eventA.date < eventB.date
      })
    },
    featuredEvents (state, getters) {
      return getters.loadedEvents.slice(0, 5)
    },
    loadedEvent (state) {
      return (eventId) => {
        return state.loadedEvents.find((event) => {
          return event.id === eventId
        })
      }
    },
    loadedPastEvent (state) {
      return (eventId) => {
        return state.loadedPastEvents.find((event) => {
          return event.id === eventId
        })
      }
    },
    loadedAllEvent (state) {
      return (eventId) => {
        return state.loadedAllEvents.find((event) => {
          return event.id === eventId
        })
      }
    }
  }
}



